<script>
import SimpleFilter from "@/components/common/display/item-actions/components/SimpleFilter.vue";
import PieChart from "@/components/common/charts/PieChart.vue";
import { form, report } from "@/api/factory.js";

export default {
  name: "FormsOverview",

  components: { SimpleFilter, PieChart },

  data() {
    return {
      formId: 0,
      formList: [],
      series: [
        {
          name: "TASK",
          color: "#7e61c8",
          data: [10, 41, 35, 51, 49, 62, 69, 91, 148, 100, 125, 30],
        },
        {
          name: "MASTER",
          color: "#cec2ed",
          data: [2, 33, 12, 23, 50, 89, 46, 80, 110],
        },
        {
          name: "WORKFLOW",
          color: "#9d61c8",
          data: [10, 41, 35, 51, 49, 62, 69, 91, 148, 100, 125, 30],
        },
        {
          name: "SLA",
          color: "#cfb1e6",
          data: [2, 33, 12, 23, 50, 89, 46, 80, 110],
        },
      ],
      chartOptions: {
        chart: {
          height: "100%",
          type: "line",
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "straight",
        },
        title: {
          text: "",
          align: "left",
        },
        // grid: {
        //   row: {
        //     colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
        //     opacity: 0.5,
        //   },
        // },
        xaxis: {
          categories: [
            "Jan",
            "Feb",
            "Mar",
            "Apr",
            "May",
            "Jun",
            "Jul",
            "Aug",
            "Sep",
            "Oct",
            "Nov",
            "Dec",
          ],
        },
      },

      pieSeries: [50, 50],
      pieChartOptions: {
        chart: {
          type: "donut",
        },
        title: {
          text: "",
          align: "left",
        },
        labels: ["Open", "Closed"],
        colors: ["#bfa0fe", "#75c1fd"],
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: "100%",
              },
              legend: {
                position: "bottom",
              },
            },
          },
        ],
        legend: {
          show: false,
        },
      },
      formCount: [
        {
          label: "Total Master Forms",
          value: "master",
          count: 0,
        },
        {
          label: "Total Workflow Forms",
          value: "workflow",
          count: 0,
        },
        {
          label: "Total SLA Forms",
          value: "sla",
          count: 0,
        },
        {
          label: "Total Task Forms",
          value: "task",
          count: 0,
        },
      ],
      userColumns: [
        {
          id: this.$nano.id(),
          name: "name",
          label: "Created User",
        },
        {
          id: this.$nano.id(),
          name: "totalForms",
          label: "Total Forms",
        },
        {
          id: this.$nano.id(),
          name: "totalEntries",
          label: "Total Entries",
        },
      ],
      userTable: [],
    };
  },

  created() {
    this.getForms();
    this.getAllFormCounts();
    // this.getAllFormGraph();
    this.getAllFormUserwiseCounts();
  },

  methods: {
    getOverviewCharts() {},

    async getForms() {
      this.$store.commit("showLoadingBar");

      let criteria = "createdBy";
      let value = this.$store.state.session.id;

      const { error, payload } = await form.getFormList(criteria, value);

      this.$store.commit("hideLoadingBar");

      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }

      // console.log(payload);

      this.formList = payload.map((group) => ({
        id: this.$nano.id(),
        label: group.value,
        value: group.id,
      }));
    },

    async getAllFormCounts() {
      this.$store.commit("showLoadingBar");

      const { error, payload } = await report.getAllFormCounts();

      this.$store.commit("hideLoadingBar");

      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }

      this.formCount.forEach((entry) => {
        const value = Object.keys(payload).find((key) => key === entry.value);

        if (value) {
          entry.count = payload[value];
        }
      });
    },

    async getAllFormGraph() {
      this.$store.commit("showLoadingBar");

      let year = new Date().getFullYear();

      const { error, payload } = await report.getAllFormtypeEntryGraph(year);

      this.$store.commit("hideLoadingBar");

      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }

      console.log("graph", payload);
    },

    async getAllFormUserwiseCounts() {
      this.$store.commit("showLoadingBar");

      const { error, payload } = await report.getUserwiseAllFormEntryCounts();

      this.$store.commit("hideLoadingBar");

      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }

      this.userTable = payload;
    },
  },
};
</script>

