<template>
  <div id="async-select-field" class="cursor-pointer">
    <FormFieldLabel
      v-if="label"
      :label="label"
      :is-mandatory="isMandatory"
      :tooltip="tooltip"
    />

    <FormFieldWrapper
      ref="wrapper"
      :is-focused="_isFocused"
      :is-clearable="_isClearable"
      :is-readonly="isReadonly"
      :is-disabled="isDisabled"
      :has-error="!!error"
      action-icon="eva-chevron-down"
      @click="showOptionPicker"
      @clear="handleClear"
    >
      <template #default>
        <!-- field -->

        <div class="async-select">
          <div v-if="value" class="value">
            {{ value }}
          </div>

          <input
            ref="searchQuery"
            type="text"
            :value="searchQuery"
            :placeholder="_placeholder"
            class="q-px-sm"
            @focus="isFocused = true"
            @blur="isFocused = false"
            @input="debouncedInput"
            @keypress.enter="handleKeypress"
          />
        </div>

        <!-- ... -->

        <!-- option picker -->

        <q-menu
          v-model="optionPicker"
          :target="$refs.wrapper"
          fit
          no-focus
          no-refocus
          no-parent-event
          transition-show="scale"
          transition-hide="scale"
        >
          <OptionPicker
            :value="value"
            :options="_options"
            :new-option="newOption"
            @select="handleSelect"
            @loadMore="getOptions"
          />
        </q-menu>

        <!-- ... -->
      </template>

      <template #action>
        <q-spinner
          v-if="isLoading"
          color="secondary"
          size="20px"
          class="q-mr-sm"
        />
      </template>
    </FormFieldWrapper>

    <FormFieldError v-if="error" :error="error" />
  </div>
</template>

<script>
import FormFieldLabel from "@/components/common/form/FormFieldLabel.vue";
import FormFieldWrapper from "@/components/common/form/field-wrapper/FormFieldWrapper.vue";
import FormFieldError from "@/components/common/form/FormFieldError.vue";
import OptionPicker from "./OptionsPicker";
import { axiosCrypto } from "@/api/axios.js";
import { debounce, lowerCase, cloneDeep, isEqual } from "lodash-es";

export default {
  name: "AsyncSelectField",

  components: {
    FormFieldLabel,
    FormFieldWrapper,
    FormFieldError,
    OptionPicker,
  },

  props: {
    value: {
      type: [String, Number],
      default: null,
    },

    label: {
      type: String,
      default: "",
    },

    placeholder: {
      type: String,
      default: "",
    },

    isMandatory: {
      type: Boolean,
      default: false,
    },

    tooltip: {
      type: String,
      default: "",
    },

    isDisabled: {
      type: Boolean,
      default: false,
    },

    isReadonly: {
      type: Boolean,
      default: false,
    },

    isClearable: {
      type: Boolean,
      default: true,
    },

    error: {
      type: String,
      default: "",
    },

    newOption: {
      type: Boolean,
      default: false,
    },

    apiPath: {
      type: String,
      default: "",
    },

    columnName: {
      type: String,
      default: "",
    },
  },

  data() {
    return {
      isFocused: false,
      optionPicker: false,
      searchQuery: "",
      options: [],
      isLoading: false,
      optionsClone: [],
    };
  },

  computed: {
    _placeholder() {
      //return this.value || this.placeholder || "Select";
      return "";
    },

    _isFocused() {
      return this.isFocused || this.optionPicker;
    },

    _isClearable() {
      return this.isClearable && !!this.value;
    },

    _options() {
      if (!this.searchQuery) {
        return this.optionsClone;
      }
      return this.optionsClone.filter((option) =>
        lowerCase(option).includes(lowerCase(this.searchQuery))
      );
    },
  },

  watch: {
    options: {
      immediate: true,
      deep: true,
      handler() {
        if (!isEqual(this.options, this.optionsClone)) {
          this.optionsClone = cloneDeep(this.options);
        }
      },
    },

    optionPicker() {
      if (!this.optionPicker) {
        this.searchQuery = "";
      }
    },

    columnName() {
      if (this.columnName) {
        this.options = [];
        this.getOptions();
      }
    },

    // searchQuery: {
    //   immediate: true,
    //   handler: "getOptions",
    // },
  },

  created() {
    this.getOptions();
  },

  methods: {
    showOptionPicker() {
      this.optionPicker = true;
    },

    handleChange(e) {
      this.searchQuery = e.target.value;
    },

    handleSelect(option) {
      this.$emit("input", option);
      this.optionPicker = false;
    },

    handleKeypress() {
      if (!this.searchQuery) {
        return;
      }
      // if (this.options.length) {
      //   this.handleSelect(this.options[0]);
      //   this.$refs.searchQuery.blur();
      //   return;
      // }
      if (this.newOption) {
        this.handleSelect(this.searchQuery);
        this.searchQuery = "";
        this.$refs.searchQuery.blur();
      }
    },

    handleClear() {
      this.$emit("input", "");
    },

    debouncedInput: debounce(function (e) {
      this.searchQuery = e.target.value;
    }, 1),

    async getOptions() {
      try {
        const response = await axiosCrypto.post(
          this.apiPath,
          JSON.stringify({
            column: this.label || this.columnName,
            keyword: this.searchQuery,
            rowFrom: this.options.length || 0,
            rowCount: this.options.length, // + 10,
          })
        );
        const { status, data } = response;

        if (status !== 200) {
          throw response;
        }

        const options = JSON.parse(data);
        this.options.push(...options);
      } catch (e) {
        console.error(e);
        this.$alert.error("Error fetching options");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#async-select-field {
  .async-select {
    min-height: 46px;
    display: flex;
    align-items: center;

    .value {
      white-space: nowrap;
      font-weight: 500;
      padding-left: 8px;
    }
  }
}
</style>
